@charset "utf-8";

@each $name, $size in $screen-size {

  @if $name == pc {
    @include helper-class();
  } @else {
    @media (max-width: $size) {
      @include helper-class("#{$name}-");
    }
  }
}

// Only display content to screen readers
// See: http://a11yproject.com/posts/how-to-hide-content/
// via. Boostrap 3

.h-invisible {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
